import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import { useParams, Link, useNavigate } from 'react-router-dom';
import swal from 'sweetalert';
import Pagination from '@mui/material/Pagination';
import { useContext, useEffect, useState } from 'react';
import TextField from '@mui/material/TextField';
import { Nav } from 'react-bootstrap';
import "../App.css"
import { Context1 } from "../App.js";
import { addCart } from '../store.js';
import { useDispatch } from 'react-redux';
import { useLike } from '../hooks/like.js';
import axios from 'axios';
import { useUserName } from '../hooks/username.js';

function Detail(props) {

  let myContext = useContext(Context1) // 보관함 해체
  let [fadeDetail, setFadeDetail] = useState('');
  let [showTimeSale, setShowTimeSale] = useState(true);
  let [inputId, setInputId] = useState(0);
  let { id } = useParams(); // 현재 url의 parameter 정보
  let idx = props.shoes.findIndex(data => (data.id).toString() == id); // id와 같은 값 발견
  const [currentPage, setCurrentPage] = useState((parseInt(id, 10)));
  let navigate = useNavigate();
  let [detailTab, setDetailTab] = useState(0);
  let dispatch = useDispatch();
  
  let [like, addLike] = useLike();
  let userName = useUserName();

  useEffect(() => {
    let watchedArr = JSON.parse(localStorage.getItem('watched'))
    if (watchedArr.findIndex((x) => x == idx) == -1) {
      watchedArr.push(idx)
      localStorage.setItem('watched', JSON.stringify(watchedArr))
    }
  }, [])

  const activeEnter = (event) => {
    if (event.key === "Enter") {
      console.log('엔터키입력')
      console.log(event.target.value)
      let copy = event.target.value;
      setInputId(copy)
    }
  }

  useEffect(() => {
    setTimeout(() => {
      setFadeDetail('end')
    }, 10);
    setTimeout(() => {
      setShowTimeSale(false)
    }, 2000);
    if (isNaN(parseInt(inputId, 10)) == true) {
      console.log('문자')
      swal("입력 오류", "숫자를 입력", "warning")
    } else {
      console.log('숫자')
    }
    return () => {
      setFadeDetail('')
    }
  }, [inputId])


  const handlePageChange = (event, value) => {
    navigate('/detail/' + (value - 1));
  };
  return (
    <div className={'start ' + fadeDetail}>
      {
        showTimeSale == true ? <div className="timeSale">지금 구매하면 50% 할인 적용!</div> : null
      }
      <TextField id="standard-basic" label="Search id" variant="standard"
        onKeyDown={(e) => activeEnter(e)} />
      <Col>
        <img src={process.env.PUBLIC_URL + '/shoes' + (idx + 1) + '.jpg'} width="30%" />
        <span onClick={() => {
          addLike()
        }}>❤️</span>{like}
        <p></p>
        <h4>{props.shoes[idx].title}</h4>
        <p>{props.shoes[idx].content}</p>
        <p>{props.shoes[idx].price}</p>
        <p>{userName}</p>
        <Button variant="outline-primary" onClick={() => {
          swal("주문 완료", "주문이 완료되었습니다!", "success");
          let addItem = props.shoes[idx];
          addItem.count = 1;
          dispatch(addCart(addItem));
        }}>🛒 주문하기</Button>
        <p></p>
        <Pagination count={props.shoes.length} page={currentPage} onChange={handlePageChange} style={{
          display: 'flex', justifyContent: 'center', alignItems: 'center',
        }} />
      </Col>

      <Nav variant="tabs" defaultActiveKey="link0">
        <Nav.Item>
          <Nav.Link eventKey="link0" onClick={() => { setDetailTab(0) }}>버튼0</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link1" onClick={() => { setDetailTab(1) }}>버튼1</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link2" onClick={() => { setDetailTab(2) }}>버튼2</Nav.Link>
        </Nav.Item>
      </Nav>
      <TabContent detailTab={detailTab} />
    </div>
  )

}

function TabContent({ detailTab }) {
  let [fade, setFade] = useState('');

  useEffect(() => {
    let a = setTimeout(() => {
      setFade('end')
    }, 10);
    return () => {
      clearTimeout(a)
      setFade('')
    }
  }, [detailTab])
  return (
    <div className={'start ' + fade}>
      {[<div>내용0</div>, <div>내용1</div>, <div>내용2</div>][detailTab]}
    </div>
  )
}

export default Detail